/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit'
import farmsConfig from 'config/constants/l2farms'
import fetchFarms2 from './fetchFarms2'
import {
  fetchFarmUserEarnings2,
  fetchFarmUserAllowances2,
  fetchFarmUserTokenBalances2,
  fetchFarmUserStakedBalances2,
} from './fetchFarmUser2'
import { Farms2State, Farm } from '../types'

const initialState: Farms2State = { data: [...farmsConfig] }

export const farmsSlice = createSlice({
  name: 'Farms2',
  initialState,
  reducers: {
    setFarmsPublicData2: (state, action) => {
      const liveFarmsData: Farm[] = action.payload
      state.data = state.data.map((farm) => {
        const liveFarmData = liveFarmsData.find((f) => f.pid === farm.pid)
        return { ...farm, ...liveFarmData }
      })
    },
    setFarmUserData2: (state, action) => {
      const { arrayOfUserDataObjects } = action.payload
      arrayOfUserDataObjects.forEach((userDataEl) => {
        const { index } = userDataEl
        state.data[index] = { ...state.data[index], userData: userDataEl }
      })
    },
  },
})

// Actions
export const { setFarmsPublicData2, setFarmUserData2 } = farmsSlice.actions

// Thunks
export const fetchFarmsPublicDataAsync2 = (p:any) => async (dispatch) => {
  const farms = await fetchFarms2()
  dispatch(setFarmsPublicData2(farms))
}
export const fetchFarmUserDataAsync2 = (account) => async (dispatch) => {
  console.log('callback');
  const userFarmAllowances = await fetchFarmUserAllowances2(account)
  console.log('callback2');
  const userFarmTokenBalances = await fetchFarmUserTokenBalances2(account)
  console.log('callback3');
  const userStakedBalances = await fetchFarmUserStakedBalances2(account)
  console.log('callback4');
  const userFarmEarnings = await fetchFarmUserEarnings2(account)
  console.log('callback5');
  const arrayOfUserDataObjects = userFarmAllowances.map((farmAllowance, index) => {
    return {
      index,
      allowance: userFarmAllowances[index],
      tokenBalance: userFarmTokenBalances[index],
      stakedBalance: userStakedBalances[index],
      earnings: userFarmEarnings[index],
    }
  })

  dispatch(setFarmUserData2({ arrayOfUserDataObjects }))
}

export default farmsSlice.reducer
